<script src="../mixum/goContentList.js"></script>
<template>
  <div class="History-container ">
    <van-cell-group class="m-t-10">
      <van-cell title="">
        <template #right-icon>
          <van-icon class="big-icon f-r" name="delete-o" @click="clearAll"/>
        </template>
      </van-cell>
      <van-cell v-for="item in $store.state.searchHistory" @click="goContentList(item,'history')">
        <template #icon>
          <van-icon class="f-r big-icon" name="underway-o"/>
          <span class="m-l-10">{{ item }}</span>
        </template>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
import goContentList from "../mixum/goContentList";

export default {
  name: "History",
  components: {},
  props: [],
  data() {
    return {}
  },
  methods: {
    clearAll() {
      this.$dialog.confirm({
        title: '',
        message: '',
      })
        .then(() => {
          this.$store.commit('clearSearchHistory');
          // on confirm
        })
        .catch(() => {
          // on cancel
        });
    }
  },
  computed: {},
  watch: {},
  mixins: [goContentList],
  created() {
  },
  mounted() {
  }
}
</script>

<style scoped lang="less">
.History-container {
  .big-icon {
    font-size: 36px;
  }
}
</style>
