<template>
  <div class="Search-container">
    <form action="/">
      <van-search
        v-model.trim="searchText"
        show-action
        action-text=""
        placeholder=""
        @search="onSearch"
        @cancel="onCancel"
        @focus="isGoList=false"
        background="#3296FA"
        clearable
      >
      </van-search>
    </form>
    <!--    ? -->
    <History v-show="!searchText&&!isGoList"></History>
    <!--    ? -->
    <SearchContent v-show="searchText&&!isGoList" :searchText="searchText"></SearchContent>
    <!--    ? -->
    <ContentList v-if="isGoList" :searchTextDetail="searchTextDetail"></ContentList>
  </div>
</template>

<script>
import History from "./component/History";
import SearchContent from "./component/SearchContent";
import ContentList from "./component/ContentList";
import goContentList from "./mixum/goContentList";

export default {
  name: "Search",
  components: {History, SearchContent, ContentList},
  props: [],
  data() {
    return {
      searchText: '',
      isGoList: false,
      searchTextDetail: ''
    }
  },
  methods: {

    onSearch() {
      this.goContentList(this.searchText, 'index');

    },
    onCancel() {
      this.$router.back();
    }
  },
  computed: {},
  watch: {},
  mixins: [goContentList],
  created() {
    this.$bus.$on('showContentList', (searchTextDetail) => {
      this.searchTextDetail = searchTextDetail;
      this.isGoList = true;
      this.searchText = searchTextDetail;
    })

  },
  mounted() {
  },
  beforeDestroy() {
    this.$bus.$off(['showContentList'])
  }
}
</script>

<style scoped lang="less">
.Search-container {
  /deep/ .van-search__action {
    color: #fff;

    &:active {
      background: unset;
    }
  }
}
</style>
