<template>
  <div class="ContentList-container">
    <div class="my-van-list">
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text=""
          @load="onLoad"
        >
          <van-cell v-for="item in list" :key="item.art_id" :title="item.title"/>
          <van-empty image="error" v-show="!list.length" description=""/>
        </van-list>
      </van-pull-refresh>
    </div>

  </div>

</template>

<script>
import getResult from "../../../api/search/getResult";

export default {
  name: "ContentList",
  components: {},
  props: ['searchTextDetail'],
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      currentResult: [],
      param: {
        q: '',
        page: 1,
        per_page: 10
      }
    }
  },
  methods: {
    async onLoad() {
      try {
        this.param.q = this.searchTextDetail;
        let {data: {data: {results}}} = await getResult(this.param);
        if (this.param.page === 1) {
          // ! 
          this.list = [];
          this.list.push(...results)
          this.refreshing = false;
        } else {
          this.list.push(...results)
        }
        this.param.page++;
        this.loading = false;
        this.currentResult = results;
        if (results.length === 0) {
          this.finished = true;
        }
        // console.log(data);
      } catch (e) {

      }
      return this.currentResult;


      /*      setTimeout(() => {
              if (this.refreshing) {
                this.list = [];
                this.refreshing = false;
              }

              for (let i = 0; i < 10; i++) {
                this.list.push(this.list.length + 1);
              }
              this.loading = false;

              if (this.list.length >= 40) {
                this.finished = true;
              }
            }, 1000);*/
    },
    onRefresh() {
      // 
      this.finished = false;
      this.param.page = 1;

      // 
      //  loading  true
      this.loading = true;
      this.onLoad();
      /*   this.onLoad().then(v => {
           this.list = [];
           this.refreshing = false;
           this.list.push(...v);
         })*/
    },
  },
  computed: {},
  watch: {},
  created() {
  },
  mounted() {
    console.log("%cmethod:mounted %cline--->62", 'color:#1abc9c;font-size:30px', 'color:#3498db;font-size:30px')
  }
}
</script>

<style scoped lang="less">
/deep/ .my-van-list {
  height: 93.5vh;
  overflow-y: auto;
}
</style>
