<template>
  <div class="SearchContent-container">
    <van-cell-group class="m-t-10">
      <van-empty v-show="!suggestList.length"
                 image="search"
                 :description="searchDescription"/>
      <van-cell v-for="item in suggestList" @click="goContentList(item,'searchContent')">
        <template #icon>
          <van-icon class="f-r big-icon" name="search"/>
          <span class="m-l-10" v-html="getHighLightHtml(item)"></span>
        </template>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
import getSuggest from "../../../api/search/getSuggest";
import tool from "../../../utils/tool";
import goContentList from "../mixum/goContentList";

export default {
  name: "SearchContent",
  components: {},
  props: ['searchText'],
  data() {
    return {
      suggestList: [],
      searchDescription: ''
    }
  },
  methods: {
    async getSuggestHandle() {
      try {
        this.searchDescription = '';
        let {data: {data: {options}}} = await getSuggest({
          q: this.searchText
        })
        this.suggestList = options.includes(null) ? [] : options;
        this.searchDescription = !this.suggestList.length ? '':'';
        // console.log(options);
      } catch (e) {
        this.$toast("search encounter exceptional")
      }
    },
    getHighLightHtml(origin, regx, replace) {
      // !  new 

      // i   g 
      return origin.replace(new RegExp(this.searchText, 'ig'), `<span style="color:#F24C4C ">${this.searchText}</span>`)

    }
  },
  computed: {},
  mixins: [goContentList],
  watch: {
    searchText: {
      handler: tool.debounce(function () {
        this.getSuggestHandle()
      }, 500)
    }
    /*    async searchText(val) {

        }*/
  },
  created() {
  },
  mounted() {
  }
}
</script>

<style scoped lang="less">
.SearchContent-container {
  .big-icon {
    font-size: 36px;
  }
}
</style>
